@use "../../sass/helpers/mixins" as *;
@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;

.context-menu {
  position: absolute;
  background-color: #333;
  margin: 0;
  font-family: $main-font;
  z-index: 1000;
  border-radius: 10px;
  max-height: 300px;
  width: 300px;
  padding: 10px;
  cursor: default;

  li {
    padding: 10px 30px;
    transition: 0.5s;
    color: white;
    border-radius: 10px;
    line-height: 1;
    text-align: right;
    font-size: 20px;
    cursor: pointer;
    &.disabled {
      pointer-events: none;
      color: #888;
    }
    &:hover,
    &:active {
      background-color: #444;
      padding-left: 20px;
    }
  }
}
