@use "../../sass/helpers/mixins" as *;
@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;
@use "sass:map";

@mixin range-parts($part) {
  @if ($part== "track") {
    background-color: $blue-1-color;
    border-radius: 5px;
    width: 100%;
    height: 10px;
  } @else if($part== "thumb") {
    border: 2px solid black;
    background-color: $blue-3-color;
    width: 18px;
    height: 18px;
    border-radius: 7px;
    // translate: 0 -4px;
    // margin-top = (track height in pixels / 2) - (thumb height in pixels /2)
    margin-top: -4px;
    cursor: pointer;
    position: relative;
    box-shadow: 0 0 10px $blue-3-color;
  } @else if($part== "progress") {
    height: 10px;
    background-color: $blue-2-color;
    border-radius: 5px;
  }
}

$chrome: (
  track: -webkit-slider-runnable-track,
  thumb: -webkit-slider-thumb,
);

$firefox: (
  track: -moz-range-track,
  thumb: -moz-range-thumb,
  progress: -moz-range-progress,
);

$ms: (
  track: -ms-track,
  thumb: -ms-thumb,
);

$all: (
  chrome: $chrome,
  firefox: $firefox,
  ms: $ms,
);

.audio-cont {
  display: flex;
  gap: 20px;
  width: 100%;
  button {
    @include centerbyflex(null);
    border: none;
    cursor: pointer;
    color: $blue-3-color;
    padding: 5px;
    &:hover {
      outline: none;
      border: none;
      background-color: none;
    }
  }

  input[type="range"] {
    flex: 1;
    appearance: none;
    background: transparent;
    position: relative;
    --progress: 50%;
    cursor: pointer;

    &:focus {
      outline: none;
    }
    @each $browser, $prefixes in $all {
      $extra: null;
      @if ($browser== "chrome") {
        $extra: none;
      }
      @each $key, $value in $prefixes {
        &::#{$value} {
          appearance: $extra;
          @include range-parts($key);
        }
        &:focus::#{$value} {
          @if ($key== "track") {
            background-color: #66a8c6;
          } @else if($key== "progress") {
            background-color: #0f809d;
          }
        }

        &:disabled::#{$value} {
          @if ($key== "progress" or $key== "thumb") {
            background-color: black;
          } @else {
            background-color: #bbb;
          }
        }
      }
    }

    &::before {
      content: "";
      position: absolute;
      width: var(--progress);
      left: 0;
      top: 50%;
      translate: 0 -50%;
      @include range-parts("progress");
    }
    &:focus::before {
      background-color: #096177;
    }
    &:disabled::before {
      background-color: grey;
    }
  }
  .play-pause-btn {
  }
  .display-track {
    display: flex;
    align-items: center;
    gap: 10px;
    flex: 1;
    span {
    }
    [type="range"] {
    }
  }
  .volume {
    position: relative;
    width: fit-content;
    height: fit-content;
    &:hover .range-cont {
      display: block;
    }
    .range-cont {
      position: absolute;
      left: 50%;
      top: 0;
      width: 100%;
      translate: 0 -10px;
      height: 100%;
      width: fit-content;
      transform-origin: 0% 50%;
      rotate: -90deg;
      transition: 0.5s;
      @include centerbyflex(null);
      padding: 5px 5px 5px 7px;
      display: none;
      [type="range"] {
        display: block;
      }
    }

    .volume-btn {
      font-size: 18px;
    }
  }
}
