@use "../../sass/variables/colors" as *;
@use "../../sass/variables/others" as *;
@use "../../sass/helpers/mixins" as *;

header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 200000;
  background-color: white;
  box-shadow: 0 0 10px black;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    .logo {
      display: flex;
      padding-block: 10px;
      align-items: center;
      gap: 15px;

      h1 {
        font-size: 20px;
        font-weight: medium;
      }
      img {
        width: 70px;
      }
    }
    nav {
      display: flex;
      font-size: 18px;
      flex: 1;
      gap: 15px;
      justify-content: flex-end;
      a {
        display: block;
        padding: 10px;
        transition: 0.5s;
        &:hover {
        }
      }
    }
  }
}
