@use "../../../sass/helpers/mixins" as *;
@use "../../../sass/variables/colors" as *;
@use "../../../sass/variables/others" as *;

.fehres {
  overflow: auto;
  max-height: 900px;
  font-family: $quran-font;
  line-height: 2.1;
  font-size: 30px;
  text-align: center;
  table {
    border-collapse: collapse;
    width: 100%;
    thead {
      tr {
        th {
          padding-bottom: 20px;
          padding-inline: 20px;
        }
      }
    }
    tbody {
      tr {
        cursor: pointer;
        transition: 0.5s;
        &:hover {
          background-color: $gold-color;
        }
        td {
          padding-block: 5px 25px;
          padding-inline: 20px;
          line-height: 1;
          text-align: center;
        }
      }
    }
  }
}
